import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"

import styles from './style.module.scss';

interface State {
  ladybugGeometry: string
  isPollinationModel: boolean
  isFocused: boolean
  syncToken: string
}

declare global {
  interface Window {
    chrome: any;
  }
}

class GetGeometry extends StreamlitComponentBase<State> {
  public state = { ladybugGeometry: "", 
  isPollinationModel: false,
  isFocused: false,
  syncToken: "" }

  public render = (): ReactNode => {

    const imgStyle = {
      margin: "0px 3px 2px 0px"
    }

    return (
      <span>
        <button
          className={styles.poButton}
          onClick={this.onClicked}
          disabled={this.props.disabled}
          onFocus={this._onFocus}
          onBlur={this._onBlur}
        >
          <span>
            <img style={imgStyle} 
            src={process.env.PUBLIC_URL + '/img/pollination.png'} 
            alt="logo" />
          </span>
          Get { this.props.args["isPollinationModel"] ? 
            "Pollination Model" : 
            "Geometry" } 
            { this.props.args["syncToken"] && 
            " (Sync)" }
        </button>
      </span>
    )
  }

  componentDidUpdate() {
    const validator = this.props.args["isPollinationModel"] ? 
      "PollinationModel" : "GeometryModel";

    if (this.state.syncToken 
      !== this.props.args["syncToken"]
      && this.state.ladybugGeometry !== ""
      )
    {
      const argToken = this.props.args["syncToken"] as string;
      if (!argToken.startsWith(validator))
      {
        return;
      }

      const newState = this.dotNetRun();
      this.setStateNow(newState);
    }
    console.log('[POLLINATION-DEBUG]: auto-update.');
  }

  private setStateNow = (newState: State): 
    void => {
    this.setState(
      (prevState) => ({ 
        ...prevState, 
        ladybugGeometry: newState.ladybugGeometry,
        isPollinationModel: newState.isPollinationModel,
        syncToken: newState.syncToken
      }),
      () => Streamlit.setComponentValue(this.state.ladybugGeometry)
    );
    console.log('[POLLINATION-DEBUG]: JSON string sent.');
  }

  private dotNetRun = (): State => {
    if (typeof window.parent.chrome != "undefined") {
      if (typeof window.parent.chrome.webview == "undefined")
      {
        console.log('[POLLINATION-DEBUG]: webview not found.');
        return this.state;
      }

      // DotNet invoke
      let geo = null;
      if (!this.props.args["isPollinationModel"]) {
        geo = window.parent.chrome.webview
        .hostObjects.sync.geometry.PassJsonGeometry();
      } else {
        geo = window.parent.chrome.webview
        .hostObjects.sync.hbjson.PassPollinationModel();
      }
      let updatedVal = (geo != null) ? 
        geo.Geometry :
        null;
  
      var newState = {
        ladybugGeometry: updatedVal,
        isPollinationModel: this.props.args["isPollinationModel"],
        syncToken: this.props.args["syncToken"]
      } as State;
  
      return newState;
    }
    else
    {
      console.log('[POLLINATION-DEBUG]: chrome not found.');
      return this.state;
    }
  }

  private onClicked = (): void => {
    const newState = this.dotNetRun();
    this.setStateNow(newState);
  }

  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

export default withStreamlitConnection(GetGeometry)
